import { h, Component } from 'preact';

import FormGroupElement from './../elements/form/FormGroup.element.preact';
import InputGroupElement from './../elements/form/InputGroup.element.preact';
import InputGroupButtonElement from './../elements/form/InputGroupButton.element.preact';

import LabelElement from './../elements/form/Label.element.preact';
import FormElement from './../elements/form/Form.element.preact';
import DateElement from './../elements/form/Date.element.preact';

class DateTest extends Component {

	render ( props, state ) {
		return <FormElement id="testForm001">
			<FormGroupElement>
				<LabelElement for="testDate001">Datum</LabelElement>
				<InputGroupElement className="date-group">
					<DateElement
						name="testDate001"
						value=""
						config={ {"monthsFull":["Januar","Februar","März","April","Mai","Juni","Juli","August","September","Oktober","November","Dezember"],"monthsShort":["Jan","Feb","Mär","Apr","Mai","Jun","Jul","Aug","Sep","Okt","Nov","Dez"],"weekdaysFull":["Sonntag","Montag","Dienstag","Mittwoch","Donnerstag","Freitag","Samstag"],"weekdaysShort":["So","Mo","Di","Mi","Do","Fr","Sa"],"today":"Heute","labelMonthNext":"Nächster Monat","labelMonthPrev":"Vorheriger Monat","labelMonthSelect":"Monat auswählen","labelYearSelect":"Jahr auswählen","format":"dd.mm.yyyy","formatSubmit":"yyyy-mm-dd","hiddenPrefix":false,"hiddenSuffix":false,"hiddenName":"{{HIDDEN_INPUT_NAME}}","firstDay":1,"min":[1950,0,1],"selectYears":30} }
						onChange={ this.handleChange.bind( this ) }
						ref={ el => { this.dateElement = el; } }
						/>
					<InputGroupButtonElement className={ [ 'date-group-btn', 'icon' ] }>
						<i class="icon icon-calendar"></i>
					</InputGroupButtonElement>
				</InputGroupElement>
			</FormGroupElement>
		</FormElement>;
	}

	componentDidMount () {
		console.log( 'COMP DID Mount' );
		const event = document.createEvent( 'HTMLEvents' );

		event.initEvent( 'message', true, true );
		event.eventName = 'message';
		event.data      = JSON.stringify( {
			type : 'formReady',
			data : {
				id : 'testForm001'
			}
		} );

		window.dispatchEvent( event );
	}

	handleChange ( e ) {
		console.log( 'CHANGE', this.dateElement.value );
	}

}

export default DateTest;



