import { h, Component } from 'preact';

export default ( { controls, onClick } ) =>
	<button
		type="button"
		class="inline-editing-trigger"
		aria-controls={ controls }
		onClick={ onClick }
		>
		<div class="icon icon-pen-ellipse"></div>
	</button>
;



