import { h, render } from 'preact';
import { Provider } from 'preact-redux';

import store from './Test.store';

import DateTest from './DateTest.component.preact';

export default class PreactTestApp {

	store = store

	init ( scope, config ) {
		render(
			<Provider store={ this.store }>
				<DateTest />
			</Provider>,
			scope
		);
	}

}



