import BaseModule from './../Base.module';
import PreactTestAppLoader from './services/PreactTestAppLoader.service';

/**
 * Module for tables to creates a responsive table with fixed first column
 * @extends {BaseModule}
 */
class PreactTestModule extends BaseModule {

	init () {
		super.init();

		PreactTestAppLoader.loadApp();
	}
}

export default PreactTestModule;



