import { IDENTIFIER } from './../constants/Identifiers.const';
import TestApp from 'PreactApp/apps/Test.app';

class PreactTestAppLoader {

	static loadApp () {
		const instance = new TestApp(),
					scope  = document.querySelector( `[data-module=${IDENTIFIER}]` );

		instance.init( scope, scope.dataset && scope.dataset.moduleConfig ? JSON.parse( scope.dataset.moduleConfig ) : {} );
	}

}

export default PreactTestAppLoader;



