import { h } from 'preact';

export default ( { id, name, value, config, ...props } ) =>
	<input
		id={ id || name }
		name={ name }
		value={ value }
		type="date"
		class="form-control form-control"
		autocomplete="off"
		autocorrect="off"
		spellcheck="false"
		data-element="datepicker"
		data-element-config={ JSON.stringify( config ) }
		{ ...props }
		/>
;



